import React from 'react';
import ReactDOM from 'react-dom';
import App from './App'
import {createStore, applyMiddleware, compose} from 'redux'
import rootReducer from './store/reducers/rootReducer'
import thunk from 'redux-thunk'
import {Provider} from 'react-redux'
import {reduxFirestore, getFirestore} from 'redux-firestore'
import {reactReduxFirebase, getFirebase} from 'react-redux-firebase'
import fbConfig from './config/fbConfig'


const store1 = createStore(rootReducer, 
    compose(
    applyMiddleware(thunk.withExtraArgument({getFirebase, getFirestore})),
    reduxFirestore(fbConfig),
    reactReduxFirebase(fbConfig, {useFirestoreForProfile: true, userProfile:'users', 
    attachAuthIsReady:true})
    ))

store1.firebaseAuthIsReady.then(()=>{
    ReactDOM.render(<Provider store={store1}><App /></Provider>, document.getElementById('root'));
})


